import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import api from "@/api/RoomsApi";

Vue.use(Vuex)
function b64EncodeUnicode(str) {
    // first we use encodeURIComponent to get percent-encoded UTF-8,
    // then we convert the percent encodings into raw bytes which
    // can be fed into btoa.
    return btoa(encodeURIComponent(str).replace(/%([0-9A-F]{2})/g,
        function toSolidBytes(match, p1) {
            return String.fromCharCode('0x' + p1);
        }));
}
function b64DecodeUnicode(str) {
    // Going backwards: from bytestream, to percent-encoding, to original string.
    return decodeURIComponent(atob(str).split('').map(function(c) {
        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
    }).join(''));
}
export default new Vuex.Store({
    state: {
        status: '',
        token: localStorage.getItem('token-rooms-ua') || '',
        user : localStorage.getItem('token-rooms-ua_user')?JSON.parse(b64DecodeUnicode(localStorage.getItem('token-rooms-ua_user'))):{},
        city:{},
        cities:[]
    },
    mutations: {
        auth_request(state){
            state.status = 'loading'
        },
        set_city(state, city){
            state.city = city;
        },
        set_cities(state, cities){
            state.cities = cities;
        },
        load_user(state, user){
            state.user = user;
        },
        auth_success(state, token, user){
            state.status = 'success'
            state.token = token
            state.user = user
        },
        auth_error(state){
            state.status = 'error'
        },
        logout(state){
            state.status = ''
            state.token = ''
            state.city = '–ö–∏—ó–≤'
        },
    },
    actions: {
        login({commit}, user){
            return new Promise((resolve, reject) => {
                commit('logout')
                commit('auth_request')
                axios({url: 'https://api.roomsua.me/auth/local', data: user, method: 'POST' })
                    .then(resp => {
                        const token = resp.data.jwt
                        const user = resp.data.user
                        localStorage.setItem('token-rooms-ua', token)
                        localStorage.setItem('token-rooms-ua_user', b64EncodeUnicode(JSON.stringify(user)))
                        axios.defaults.headers.common['Authorization'] = token
                        commit('auth_success', token, user)
                        resolve(resp)
                    })
                    .catch(err => {
                        commit('auth_error')
                        localStorage.removeItem('token-rooms-ua')
                        reject(err)
                    })
            })
        },
        loadCities({commit}){
            return new Promise((resolve, reject) => {
                commit('set_cities')
                api('cities').getAll().then(cities => {
                    commit('set_cities', cities);
                    commit('set_city',cities[0]);
                    resolve(cities);
                } ).catch(err => {
                    reject(err);
                })
            })
        },
        loadUser(){

            return localStorage.getItem('token-rooms-ua_user')
        },
        register({commit}, user){
            return new Promise((resolve, reject) => {
                commit('logout')
                 axios({url: 'https://api.roomsua.me/auth/local', data: user, method: 'POST' })
                    .then(resp => {
                        const token = resp.data.jwt
                        const user = resp.data.user
                        localStorage.setItem('token-rooms-ua', token)
                        localStorage.setItem('token-rooms-ua_user', b64EncodeUnicode(JSON.stringify(user)))
                        axios.defaults.headers.common['Authorization'] = token
                        commit('auth_success', token, user)
                        resolve(resp)
                        return user;
                    })
                    .catch(err => {
                        commit('auth_error', err)
                        localStorage.removeItem('token-rooms-ua')
                        reject(err)
                    })
            })
        },
        logout({commit}){
            return new Promise((resolve) => {
                commit('logout')
                localStorage.removeItem('token-rooms-ua')
                localStorage.removeItem('token-rooms-ua_user')
                delete axios.defaults.headers.common['Authorization']
                resolve()
            })
        }
    },
    getters : {
        isLoggedIn: state => !!state.token,
        user: state => state.user,
        authStatus: state => state.status,
        getCity:state => state.city,
        getCities:state => state.cities,
    }
})
