import axios from 'axios'
import qs from 'qs'
// axios.defaults.headers.common['Authorization'] = "Bearer "+ localStorage.getItem('token-rooms-ua');
const  http = axios.create({    
    baseURL: `https://api.roomsua.me`,
});

const api = (modelName)=>{
        return {
            getOne : (id)=>{
               return http.get(`/${modelName}/${id}`).then(response => response.data).catch(err => err);
            },
            getAll : (filters)=>{
                if(filters) {
                    return http.get(`/${modelName}?${(new URLSearchParams(qs.stringify(filters)).toString())}`).then(response => response.data).catch(err => err);
                } else {
                    return http.get(`/${modelName}`).then(response => response.data).catch(err => err);
                }
            },
            update : (id, body)=>{
                return http.put(`/${modelName}/${id}`, JSON.stringify(body)).then(response => response.data).catch(err => err);
            }
        }
    }
export default api;
