import Vue from 'vue'
import VueRouter from 'vue-router'
import MainPage from "@/views/MainPage";
import store from '../store/store'



Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Main',
        component: MainPage
    },
    {
        path: '/login',
        name: 'Login',
        component: MainPage
    },
    {
        path: '/apartments',
        name: 'Apartments',
        component: () => import(/* webpackChunkName: "apartments" */ '../views/FindApartment.vue')
    },
    {
        path: '/apartments/:id',
        name: 'Apartments by id',
        component: () => import(/* webpackChunkName: "apartments" */ '../views/ApartmentPage.vue')
    },
    {
        path: '/tariffs',
        name: 'Tariffs',
        // meta: {
        //     requiresAuth: true
        // },
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "tarrifs" */ '../views/Tariffs.vue')

    },
    {
        path: '/personal',
        name: 'PersonalCompilation',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "helper" */ '../views/PersonalCompilation.vue')
    }
]

const router = new VueRouter({
    routes
})
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.isLoggedIn) {
            next()
            return
        }
        next('/login')
    } else {
        next()
    }
})
export default router
